<template>
  <div class="news_list_box base_width">
    <div class="item" v-for="(item,index) in list.slice((pageNum - 1)*pageSize,pageNum*pageSize)" :key="index">
      <img @click="$router.push({path:item.url})" class="img" :src="item.img" alt />
      <div class="content">
        <h3 @click="$router.push({path:item.url})" class="overflow_1">{{item.title}}</h3>
        <h5 class="font-weight-normal">{{item.tips}}</h5>
        <p class="overflow_4">
          {{item.content1}}
