</template>

<script>
import newsList from "./news_list";
import api from "@/api";
import Pagination from "@/components/common/pagination.vue";
export default {
  name: "News_School",
  components: {
    Pagination
  },
  data() {
    return {
      list: [],
      pageSize: 6,
      pageTotal: null,
      pageNum: 1,
      moduleType:"公司动态"
    };
  },
  created() {
    this.getNewsList();
    // console.log(this.$route)
  },
  mounted() {
    // this.getData()
  },
  methods: {
    getNewsList() {
      this.list = newsList.school;
      this.pageTotal = newsList.school.length;
    },
    pageCallBlack(index) {   
      this.pageNum = index;
      console.log(this.pageNum)     
    },
    
    
    getData() {//获取资讯列表
    const that = this;
      this.$axios.post('/v1/information/queryInfoDynamicsPublishedPage', {
          
            moduleType:"投资学堂",//投资学堂
            pageNum:1,
            pageSize:100
          
        })
      
      .then(function(res) {
          console.log(res.data.list)
          // that.list = res.data.list;
          that.pageTotal = res.data.total;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    goToDetail(id) {  
      this.$router.push({
        name:'News_School_Detail',
        params:{
          id:id
        }
        })
    }
  }
};
</script>

<style lang="scss" scoped>
.news_list_box {
  margin: 0 auto;
  .item {
    padding: 36px 0px;
    border-bottom: 1px solid #dadce0;
    box-sizing: border-box;
    transition: all ease 0.5s;
    -moz-transition: all ease 0.5s;
